<template>
  <div class="login_container">
    <el-input
      class="username"
      placeholder="请输入用户名"
      v-model="username"
      clearable>
    </el-input>
    <el-input
      class="pwd"
      placeholder="请输入密码"
      v-model="password"
      type="password"
      clearable>
    </el-input>
    <el-button type="primary" class="login_btn" @click="login">登陆</el-button>
  </div>

</template>

<script>
import {login} from '../../api/login'

export default {
  name: 'login',
  data () {
    return {
      username: '',
      password: ''
    }
  },
  methods: {
    login: function () {
      login.doLogin(this.username, this.password, r => {
        console.log('json: ' + JSON.stringify(r))
        let result = JSON.parse(JSON.stringify(r))
        if (result.status === -1) {
          this.$message({
            showClose: true,
            message: '用户不存在',
            type: 'success'
          })
        } else if (result.status === 0) {
          this.$message({
            showClose: true,
            message: '登录成功',
            type: 'success'
          })
          this.$store.commit('setUserName', result.data.userName)
          this.$store.commit('setCurrentUserId', result.data.userId)
          this.$store.commit('setLoginName', result.data.loginName)
          // this.$router.replace({name: 'main', params: {userInfo: result.data.userInfo}})
          this.$router.replace('main')
        } else if (result.status === 1) {
          this.$message({
            showClose: true,
            message: '密码错误',
            type: 'success'
          })
        } else {
          this.$message({
            showClose: true,
            message: '其他错误',
            type: 'success'
          })
        }
      })
    }
  }
}
</script>

<style scoped>
  .login_container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .username {
    width: 20%;
    min-width: 220px;
  }

  .pwd {
    width: 20%;
    min-width: 220px;
    margin-top: 10px;
  }

  .login_btn {
    width: 10%;
    min-width: 120px;
    margin-top: 10px;
  }
</style>



